.icono{
    text-align: center;
    svg{
        height: 15rem;
    }
    h3{
        text-transform: uppercase;
        font-weight: 400;
    }
    p {
        line-height: 3rem;
        border: 2px solid lighten($tonoPrincipal, 30);
        padding: 1rem 2rem;
        border-radius: 15px;
        background-color: lighten( $tonoClaro, 50);
    }
}

.icon{
    padding: 2rem;
    &:hover{
        background-color: $tonoSecundario;
        border-radius: 50%;
        stroke : $blanco;
    }
}

.iconos-nosotros{
    @include tablet {
        @include grid (3, 2rem);    
    }    
}
