@include tablet {
    .seccion-inferior{
        display: grid;
        grid-template-columns: 2fr 1fr;
        column-gap: 2rem;
    }
}

.entrada-blog {
    margin-bottom: 2rem;
    @include tablet {
        display: grid;
        grid-template-columns: 1fr 2fr;
        column-gap: 2rem;
    }

    &:last-of-type{
        margin-bottom: 0;
    }
    
    img{
        border-radius: 15px;
    }
}

.texto-entrada {
    a{
        color: $negro;
    }
    h4{
        margin: 0;
        line-height: 1.4;
        margin-top: 2rem;
        &::first-letter{
            text-transform: capitalize;
        }
        @include tablet {
            margin-top: 0;
        }
        &::after{
            content: '';
            display: block;
            width: 20rem;
            height: .5rem;
            background-color: $tonoPrincipal;
            margin-top: .5rem;
        }
    }
    span{
        color: $verde;
        font-weight: bold;
    }
    
}