}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #06060a;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.contenedor-footer .navegacion {
  visibility: visible;
  opacity: 1;
  height: auto;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 80rem;
}

.alinear-derecha {
  display: flex;
  justify-content: flex-end;
}

.texto-negrita {
  font-weight: 500;
}

.boton-azul {
  background-color: #185574;
  color: #f3f3f3;
  font-weight: bold;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
  border-radius: 10px;
  border: none;
}
.boton-azul:hover {
  background-color: #14465f;
  cursor: pointer;
}

.boton-azul-block {
  background-color: #185574;
  color: #f3f3f3;
  font-weight: bold;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border-radius: 10px;
  border: none;
}
.boton-azul-block:hover {
  background-color: #14465f;
  cursor: pointer;
}

.boton-verde {
  background-color: #08ba52;
  color: #f3f3f3;
  font-weight: bold;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
  border-radius: 10px;
  border: none;
}
.boton-verde:hover {
  background-color: #07a247;
  cursor: pointer;
}

.footer {
  padding: 2rem 0 0.5rem;
  background-color: #333333;
}
.footer .contenedor-footer {
  padding-top: 2.5rem;
}
.footer .copyright {
  text-align: center;
  color: #f3f3f3;
}

.no-webp .inicio {
  background-image: linear-gradient(rgba(0, 0, 0, 0.75), rgba(0, 0, 0, 0.75)), url("../img/header.jpg");
}

.webp .inicio {
  background-image: linear-gradient(rgba(0, 0, 0, 0.75), rgba(0, 0, 0, 0.75)), url("../img/header.webp");
}

.no-webp .logo_principal {
  background-image: url("../img/logo.png");
}

.webp .logo_principal {
  background-image: url("../img/logo.webp");
}

.logo_principal {
  width: 20rem;
  height: 20rem;
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  border-radius: 15px;
}

.header {
  background-color: #333333;
  padding: 1rem 0 3rem 0;
}
.header.inicio {
  background-position: center center;
  background-size: cover;
  background-repeat: no-repeat;
}
@media (min-width: 768px) {
  .header.inicio {
    min-height: 70rem;
  }
}
@media (min-width: 1024px) {
  .header.inicio {
    background-position: center 60%;
  }
}
.header.inicio .contenido-header {
  min-height: 50rem;
}
@media (min-width: 768px) {
  .header.inicio .contenido-header {
    min-height: 70rem;
  }
}

.contenido-header {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.contenido-header h1 {
  color: #f3f3f3;
  font-weight: 200;
  max-width: 80rem;
  text-align: left;
  font-size: 2.5rem;
  line-height: 1.5;
}
@media (min-width: 768px) {
  .contenido-header h1 {
    font-size: 4rem;
  }
}
.contenido-header h1 span {
  font-weight: 600;
}

.navegacion a {
  color: #f3f3f3;
  transition: color 0.5s ease;
}
.navegacion a:hover {
  color: #2685b6;
}

.derecha {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 2rem;
}
.derecha .dark-mode-boton {
  width: 5rem;
  margin-bottom: 2rem;
  cursor: pointer;
}
.derecha .dark-mode-boton path {
  pointer-events: visiblePainted;
  transition: fill 0.3s ease-in-out;
}
.derecha .dark-mode-boton:hover path {
  fill: #2685b6;
}
@media (min-width: 768px) {
  .derecha .dark-mode-boton {
    margin-top: 0;
  }
}
@media (min-width: 768px) {
  .derecha {
    align-items: flex-end;
  }
}

.barra {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 3rem;
}
@media (min-width: 768px) {
  .barra {
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-start;
  }
}

.navegacion {
  visibility: hidden;
  opacity: 0;
  transition: visibility 0.3s ease-in-out, opacity 0.5s linear, height 5s ease-in-out;
  height: 0;
  text-align: center;
}
@media (min-width: 768px) {
  .navegacion {
    visibility: visible;
    opacity: 1;
    display: flex;
    align-items: center;
  }
}
.navegacion a {
  color: #f3f3f3;
  text-decoration: none;
  font-size: 2rem;
  display: block;
  text-align: center;
}
@media (min-width: 768px) {
  .navegacion a {
    margin-right: 2rem;
  }
}
.navegacion a:last-of-type {
  margin-right: 0;
}

@media (min-width: 768px) {
  .mobile-menu {
    display: none;
  }
}
.mobile-menu img, .mobile-menu svg {
  width: 5rem;
  margin: 0 auto;
}

.mostrar {
  visibility: visible;
  opacity: 1;
  height: auto;
}

.icono {
  text-align: center;
}
.icono svg {
  height: 15rem;
}
.icono h3 {
  text-transform: uppercase;
  font-weight: 400;
}
.icono p {
  line-height: 3rem;
  border: 2px solid #8ec9e7;
  padding: 1rem 2rem;
  border-radius: 15px;
  background-color: #f5fcfd;
}

.icon {
  padding: 2rem;
}
.icon:hover {
  background-color: #0c2a3a;
  border-radius: 50%;
  stroke: #f3f3f3;
}

@media (min-width: 768px) {
  .iconos-nosotros {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}

.anuncio {
  background-color: #eaf5fb;
  border: 1px solid #2bb3c8;
  border-radius: 15px;
  display: flex;
  flex-direction: column;
}
.anuncio .seccion_img {
  flex: 1;
  position: relative;
}
.anuncio .seccion_img img {
  border-radius: 13px 13px 0 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
@media (min-width: 768px) {
  .anuncio .seccion_img img {
    min-height: 150px;
    margin-bottom: 150px-height(image);
  }
}
.anuncio p, .anuncio h3 {
  margin: 0;
}
.anuncio .contenido-anuncio {
  flex: 4;
  display: flex;
  flex-direction: column;
  padding: 1rem;
}
.anuncio .contenido-anuncio h3 {
  font-weight: 700;
  text-transform: capitalize;
  color: #2685b6;
}
@media (min-width: 768px) {
  .anuncio .contenido-anuncio h3 {
    min-height: 2.5rem;
  }
}
.anuncio .contenido-anuncio p {
  margin: 1rem;
}

.precio {
  color: #08ba52;
  font-weight: bold;
  font-size: 3rem;
}

.iconos-caracteristicas {
  display: flex;
  padding: 0;
  max-height: 40rem;
}
.iconos-caracteristicas li {
  list-style: none;
  flex: 1;
  display: flex;
}
.iconos-caracteristicas li img {
  flex: 0 0 3rem;
}
.iconos-caracteristicas li p {
  font-weight: 900;
}

.seccion_b {
  margin-top: auto;
}

@media (min-width: 768px) {
  .contenedor-anuncios {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
    align-items: stretch;
  }
}
.no-webp .imagen-contacto {
  background-image: linear-gradient(rgba(0, 0, 0, 0.75), rgba(0, 0, 0, 0.75)), url("../img/encuentra.jpg");
}

.webp .imagen-contacto {
  background-image: linear-gradient(rgba(0, 0, 0, 0.75), rgba(0, 0, 0, 0.75)), url("../img/encuentra.webp");
}

.imagen-contacto {
  background-position: center center;
  background-size: cover;
  padding: 5rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.imagen-contacto h2 {
  font-weight: bold;
}
.imagen-contacto h2, .imagen-contacto p {
  color: #f3f3f3;
}
.imagen-contacto p {
  font-size: 2rem;
  padding: 0 2rem;
}

@media (min-width: 768px) {
  .seccion-inferior {
    display: grid;
    grid-template-columns: 2fr 1fr;
    column-gap: 2rem;
  }
}
.entrada-blog {
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .entrada-blog {
    display: grid;
    grid-template-columns: 1fr 2fr;
    column-gap: 2rem;
  }
}
.entrada-blog:last-of-type {
  margin-bottom: 0;
}
.entrada-blog img {
  border-radius: 15px;
}

.texto-entrada a {
  color: #06060a;
}
.texto-entrada h4 {
  margin: 0;
  line-height: 1.4;
  margin-top: 2rem;
}
.texto-entrada h4::first-letter {
  text-transform: capitalize;
}
@media (min-width: 768px) {
  .texto-entrada h4 {
    margin-top: 0;
  }
}
.texto-entrada h4::after {
  content: "";
  display: block;
  width: 20rem;
  height: 0.5rem;
  background-color: #2685b6;
  margin-top: 0.5rem;
}
.texto-entrada span {
  color: #08ba52;
  font-weight: bold;
}

.testimoniales h3 {
  font-weight: bold;
}

.testimonial {
  background-color: #185574;
  border-radius: 0px 15px 0px 15px;
  padding: 0;
}
.testimonial blockquote {
  margin: 0;
  color: #f3f3f3;
  font-size: 2.4rem;
  line-height: 3rem;
  padding: 2rem 2rem 2rem 6rem;
  position: relative;
}
.testimonial blockquote::before {
  content: "";
  background-image: url("../img/estrella.svg");
  background-repeat: no-repeat;
  width: 4rem;
  height: 4rem;
  display: block;
  position: absolute;
  left: 1rem;
}
.testimonial p {
  color: #f3f3f3;
  padding: 0rem 2rem 1rem;
  margin-top: -0.5rem;
}

.formulario fieldset {
  border-radius: 15px;
}
.formulario legend {
  font-size: 2rem;
  font-weight: 400;
  color: #333333;
}
.formulario p {
  font-size: 1.7rem;
  font-weight: bold;
  color: #333333;
  margin: 2rem 0 0 0;
}
.formulario label {
  font-weight: bold;
  text-transform: uppercase;
  display: block;
}
@media (min-width: 768px) {
  .formulario label {
    display: inline-block;
    margin-right: 2rem;
  }
}
.formulario input:not([type=submit]):not([type=radio]),
.formulario textarea,
.formulario select {
  padding: 1rem 2rem;
  width: 100%;
  background-color: #f3f3f3;
  border: 1px solid #333333;
  border-radius: 10px;
  margin-bottom: 2rem;
}
.formulario input[type=radio] {
  margin: 0;
  width: auto;
}
.formulario .opciones-contacto {
  max-width: 30rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.formulario textarea {
  height: 20rem;
}

.contenedor img {
  border-radius: 15px;
}

h1, h2 {
  font-weight: bold;
}

.titulo_nosotros {
  display: block;
  margin: 0 auto;
  width: 100%;
}
.titulo_nosotros h1 {
  font-weight: 400;
}

.descripcion {
  padding: 2rem;
}
@media (min-width: 768px) {
  .descripcion {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.descripcion img {
  border-radius: 15px;
  margin-top: 50%;
}

.parrafo blockquote {
  font-weight: bold;
  font-size: 2.5rem;
  text-align: center;
}

.dark-mode {
  background-color: #262626;
}
.dark-mode p:not(.precio), .dark-mode blockquote, .dark-mode h1, .dark-mode h2, .dark-mode h3, .dark-mode h4, .dark-mode .formulario legend, .dark-mode label {
  color: #f3f3f3;
}
.dark-mode p:not(.seccion_a p):not(.texto-entrada p):not(.testimonial p):not(.imagen-contacto p):not(.copyright):not(.parrafo p):not(.forma-contacto p), .dark-mode .anuncio {
  background-color: #10394f;
}
.dark-mode .iconos-caracteristicas li img {
  filter: invert(100%);
}