.derecha{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 2rem;

    .dark-mode-boton {
        width: 5rem;
        margin-bottom: 2rem;
        cursor: pointer;
        path {
            pointer-events: visiblePainted;
            transition: fill .3s ease-in-out;
        }
        &:hover path{
            fill: #2685b6;
        }
        @include tablet {
            margin-top: 0;
        }
    }
    @include tablet {
        align-items: flex-end;
    }
}

.barra {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 3rem;
    @include tablet {
        flex-direction: row;
        justify-content: space-between;
        align-items: flex-start;
    }
}

.navegacion {
    visibility: hidden;
    opacity: 0;
    transition: visibility .3s ease-in-out, opacity .5s linear, height 5s ease-in-out;
    height: 0;

    @include tablet {
        visibility: visible;
        opacity: 1;
        display: flex;
        align-items: center;
    }
    text-align: center;  
    a {
        color: $blanco;
        text-decoration: none;
        font-size: 2rem;
        display: block;
        text-align: center;

        @include tablet {
            margin-right: 2rem;
        }

        &:last-of-type{
            margin-right: 0;
        }
    }
}

// Menu de hamburguesa
.mobile-menu{
    @include tablet{
        display: none;
    }
    img, svg{
        width: 5rem;
        margin: 0 auto;
    }
}

.mostrar {
    visibility: visible;
    opacity: 1;
    height: auto;
}
