.no-webp .inicio {
    background-image: linear-gradient(rgba(0, 0, 0, 0.75), rgba(0, 0, 0, 0.75)), url('../img/header.jpg');
}

.webp .inicio {
    background-image: linear-gradient(rgba(0, 0, 0, 0.75), rgba(0, 0, 0, 0.75)), url('../img/header.webp');
}

.no-webp .logo_principal{
    background-image: url('../img/logo.png');
}

.webp .logo_principal{
    background-image: url('../img/logo.webp');
}

.logo_principal{
    width: 20rem;
    height: 20rem;
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
    border-radius: 15px;
}

.header {
    background-color: $grisOscuro;
    padding: 1rem 0 3rem 0;

    &.inicio {
        background-position: center center;
        background-size: cover;
        background-repeat: no-repeat;

        @include tablet {
            min-height: 70rem;
        }

        @include desktop {
            background-position: center 60%;
        }

        .contenido-header {
            min-height: 50rem;
            @include tablet {
                min-height: 70rem;
            }
        }
    }
}

.contenido-header{
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    h1 {
        color: $blanco;
        font-weight: 200;
        max-width: 80rem;
        text-align: left;
        font-size: 2.5rem;
        line-height: 1.5;

        @include tablet {
            font-size: 4rem;
        }

        span {
            font-weight: 600;
        }
    }
}

.navegacion{
    a{
        color: $blanco;
        transition: color .5s ease;
        &:hover{
            color: $tonoPrincipal;
        }
    }
}