.testimoniales{
    h3{
        font-weight: bold;
    }
}


.testimonial{
    background-color: $tonoOscuro;
    border-radius: 0px 15px 0px 15px;
    padding: 0;

    blockquote{
        margin: 0;
        color: $blanco;
        font-size: 2.4rem;
        line-height: 3rem;
        padding: 2rem 2rem 2rem 6rem;
        position: relative;

        &::before{
            content: '';
            background-image: url('../img/estrella.svg');
            background-repeat: no-repeat;
            width: 4rem;
            height: 4rem;
            display: block;
            position: absolute;
            left: 1rem;
        }
    }

    p{
        color: $blanco;
        padding: 0rem 2rem 1rem;
        margin-top: -.5rem;
    }
}