.formulario{
    fieldset{
        border-radius: 15px;
    }
    legend{
        font-size: 2rem;
        font-weight: 400;
        color: $grisOscuro;
    }
    p{
        font-size: 1.7rem;
        font-weight: bold;
        color: $grisOscuro;
        margin: 2rem 0 0 0;
    }
    label{
        font-weight: bold;
        text-transform: uppercase;
        display: block;
        @include tablet {
            display: inline-block;
            margin-right: 2rem;
        }
    }
    input:not([type='submit']):not([type='radio']), 
    textarea,
    select {
        padding: 1rem 2rem;
        // display: block;
        width: 100%;
        background-color: $blanco;
        border: 1px solid $grisOscuro;
        border-radius: 10px;
        margin-bottom: 2rem;
    }
    input[type='radio']{
        margin: 0;
        width: auto;
    }

    .opciones-contacto {
        max-width: 30rem;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    textarea {
        height: 20rem;
    }
}

.contenedor img{
    border-radius: 15px;
}

h1, h2 {
    font-weight: bold;
}