.dark-mode {
    background-color: darken($grisOscuro, 5%);
    p:not(.precio),blockquote, h1, h2, h3, h4,.formulario legend, label {
        color: $blanco;
    }
    p:not(.seccion_a p):not(.texto-entrada p):not(.testimonial p):not(.imagen-contacto p):not(.copyright):not(.parrafo p):not(.forma-contacto p), .anuncio {
        background-color: lighten($tonoSecundario, 5%);
    }
    .iconos-caracteristicas li{
        img {
            filter: invert(100%);
        }
    }
}