.anuncio{
    background-color: lighten( $tonoPrincipal , 52);
    border: 1px solid $tonoClaro;
    border-radius: 15px;
    display: flex;
    flex-direction: column;

    .seccion_img{
        flex: 1;
        position: relative;

        img{
            border-radius: 13px 13px 0 0;
            width: 100%;
            height: 100%;
            object-fit: cover;
            
            @include tablet{
                min-height: 150px;

                // Calcula el margen inferior necesario
                $resultado: ($margen_inferior - height(image));
                @if $margen_inferior > 0 {
                    margin-bottom: $resultado;
                }
            }
        }
    }
    p, h3 {
        margin: 0;
    }
    .contenido-anuncio{
        flex: 4;
        display: flex;
        flex-direction: column;
        padding: 1rem;
        h3 {
            font-weight: 700;
            text-transform: capitalize;
            color: $tonoPrincipal;
            @include tablet {
                min-height: 2.5rem;
            }
        }
        p{
            margin: 1rem;
        }
    }
}

.precio{
    color: $verde;
    font-weight: bold;
    font-size: 3rem;
}

.iconos-caracteristicas{
    display: flex;
    padding: 0;
    max-height: 40rem;
    li {
        // padding: 0 .5rem;
        list-style: none;
        flex: 1;
        display: flex;
        img {
            flex: 0 0 3rem;
            // margin: 0 .5rem;
        }
        p {
            font-weight: 900;
        }
    }
}

.seccion_b{
    margin-top: auto;
}

@include tablet {
    .contenedor-anuncios{
        @include  grid(3, 2rem);
        align-items: stretch;
    }
}